import { User } from "~~/server/src/user";

export interface UserState {
  selectedUser: User | null;
}

export const useUserStore = defineStore("users-store", {
  state: (): UserState => ({
    selectedUser: null,
  }),

  actions: {
    setSelectedUser(payload: User) {
      console.log(payload);
      this.selectedUser = payload;
    },
  },
  persist: true,
});
